@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    overflow-x: auto;
    overflow-y: auto;
    background-color: #0d1019;
    font-family: 'Audiowide';
}

.contact-us {
    width: 100%;
    height: 100%;
}

.contact-us-div {
    display: inline-flex;
    text-align: center;
}

.contact-us-div-left {
    display: inline-block;
    justify-content: center;
    padding: 1vh;
    width: 50vw;
    height: auto;
}

.contact-us-div-right {
    display: inline-block;
    justify-content: center;
    padding: 1vh;
    width: 50vw;
    height: auto;
}

@media (max-width: 800px) {
    .contact-us-div {
        display: inline-block;
        width: 100%;
        padding: 0vh;
        margin: 0vh;
        
    }

    .contact-us-div-left, .contact-us-div-right {
        width: 100%;
        padding: 0vh;
        margin: auto;
    } 
}