@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    font-family: "Audiowide";
}

.contact-box-div {
    position: relative;
    display: inline-flex;
    justify-content: center;
    width: 33vw;
    height: 33vh;
    margin: 3vh;
    background-color: #252830;
    border-radius: 1vh;
    border: .25vh solid #0984e3;
    min-width: 350px;
}

.contact-box-header {
    position: absolute;
    display: block;
    width: auto;
    max-width: 90%;
    height: auto;
    border-bottom: .1vh solid #0984e3;
    
}

.body-div {
    display: inline-block;
    width: 100%;
    margin-bottom: 5vh;
}

.contact-box-body {
    position: relative;
    top: 33%;
    display: block;
    height: auto;
    width: 100%;
    
}

.contact-box-body h2 {
    color: white;
    font-size: 2.5vh;
    padding: 1vh;
    font-weight: 100;
}

.contact-box-div h3 {
    color: white;
    font-size: 3.5vh;
    margin: 1vh;
    font-weight: 100;
}

.contact-box-div:hover {
    cursor: pointer;
    transform: scale(1.1); 
    animation: content_pulse 2s infinite;
    transition: ease-in-out .25s;
}


.contact-box-div:hover h3{
    color: #0984e3;
}

.contact-box-div:hover .contact-box-header{
    border-bottom: .1vh solid white;
}

.icon-div {
    position: absolute;
    display: inline-block;
    bottom: 0vh;
    width: 100%;
    height: 33%;
}

.contact-box-div:hover .contact-icon {
    color: #0984e3;
    transform: scale(1.2);
    transition: ease-in-out .25s;
}

.contact-icon {
    color: white;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}

@media (max-width: 800px) 
{
    .contact-box-div {

        margin: 1vh;
    }

    .icon-div {
        position: absolute;
        bottom: 0vh;
    }
}